body {
  background: url('./images/jamaal-cooks-sp4dq3f825M-unsplash.jpg');
  background-size: contain;
  background-position: center;
  margin: 5px;
  font-family: 'Inter', sans-serif;
}

.wrapper {
  max-width: 550px;
  height: auto;
  background-color:#fff;
  margin: 0 auto;
}

.navbar {
  background-color: #F55A5A;
  color: #fff;
  height: 55px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.075em;
}


.navbar img {
  float: left;
  padding-left: 35%;
  margin-top: 12px;
  padding-right: 8px;
}

.navbar p {
  float: left;
  margin-top: 13px;
}

p {
  margin-top: 0px;
  margin-bottom: 4px;
}

h1 {
  margin-top: 0px;
  margin-bottom: 4px;
}

.line {
  background-color: #e6e6e6;
  height: 1px;
  border: none;
}

.cards {
  padding-left: 8%;
  padding-top: 10%;
  padding-right: 5%;
}

.card {
  padding-bottom: 35px;
}

.card--content {
  margin-left: 30%;
}

.card--image-contain {
  width: 125px;
  height: 168px;
  border-radius: 5px;
  overflow: hidden;
  float: left;
}

.card--image-contain img {
  width: auto;
  height: 168px;
  position: relative;
  right: 65px;
}

.card--location-img {
  margin-top: 5px;
  width: 7px;
  height: 9.55px;
  padding-right: 5px;
}

.card--title {
  font-size: 25px;
}

.card--timeperiod {
  display: flex;
  font-weight: bold;
  font-size: 10.24px;
  margin-top: 30px;
}

.location {
  display: flex;
}

.card--location {
  letter-spacing: 2px;
  font-size: 10.24px;
  margin-top: 3px;
  padding-right: 10px;
  font-weight: 300;
}

.card--maps {
  color: #918E9B;
  font-size: 10.24px;
  margin-top: 3px;
}

.card--description {
  font-size: 10.24px;
  padding-bottom: 35px;
}






